.loading-circle {
    height: 1rem;
    width: 1rem;
    animation: spin 1s linear infinite;
    fill: #57534e;
    color: #e7e5e4;
}

@keyframes spin {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}

.loading-circle path:first-child {
    fill: currentColor;
}

.loading-circle path:last-child {
    fill: currentFill;
}